.container-header {
  background-color: #fff;
  width: 100%;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  /* border-bottom: 1px solid #EDEDF1; */
  box-shadow: rgba(100, 100, 111, 0.2) 0px -2px 10px 0px; 
}

.container-header a {
  text-decoration: none;
  color: #000;
}

.container-header .title-header a {
  /* font-family: 'Kaisei Tokumin', serif; */
  font-size: 2rem;
  font-weight: 500;
  text-transform: uppercase;
  color:#000;
  text-decoration: none;
}

.container-header .title-header  .logo {
  width: 27px;
}
.container-header .title-header img, svg {
  width: 100%;
} 

.container-header .buttons {
  width: 37%;
  display: flex;
  justify-content: space-between;
}

.container-header .buttons a{
  text-decoration: none;
  color: #000;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 16px;
}

.container-header .buttons a:hover {
  color:#B61919;
  transition: 0.5s;
}

.container-header .social-media {
  width: 7%;
  display: flex;
  justify-content: space-between;
}

.container-header .nav-addcity a{
  text-decoration: none;
  color: rgb(128, 128, 128);
  letter-spacing: 2px;
  text-transform: uppercase;
}
.container-header .nav-addcity a:hover{
  color: #B61919;
}

.container-header .social-media .icon-social-media img {
  width: 27px;    
}

.container-header .nav-items a:nth-of-type(1) {
  margin-right: 1.2rem;
}

.container-header .nav-items:nth-of-type(2) {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-logo {
  display: block;
  width: 3.5rem;
}

.container-logo img {
  width: 100%;
  height: 100%;
}

.icon-header {
  background-color: var(--color-gray);
  width: 35px;
  height: 35px;
  padding:5px;
  border-radius: 50%;
}