#page {
  height: 100%;
  margin: 0 auto;
  width: 70%;
  font-size: 1.9vw;
  color: #fff;
}

#container {
  display: table-cell;
  vertical-align: middle;
}

#page h1,
#page h2, #page p {
  font-weight: normal;
  padding: 0;
  margin: 25px 0;
  margin-top: 0;
  font-weight: 300;
}

#page h1 {
  font-size: 6.5em;
  margin-bottom: 10px;
}

#page h2 {
  font-size: 1.5em;
}

#details {
  display: flex;
  flex-flow: row;
  flex-wrap: nowrap;
  padding-top: 10px;
}

#qr {
  flex: 0 1 auto;
}

#image {
  background: white;
  padding: 5px;
  line-height: 0;
}

#image img {
  width: 9.8em;
  height: 9.8em;
}

#stopcode {
  padding-left: 10px;
  flex: 1 1 auto;
}

@media (min-width: 840px) {
  #page {
      font-size: 140%;
      width: 800px;
  }
}
